.HSuper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 0rem;
  margin: 0px;
  height: 90vh;
  border-bottom: solid 4px black;

  background-image: url(../../images/herobg.png);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  /* border: solid greenyellow 1px; */
}

.HContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80%;

  /* border: solid greenyellow 1px; */
}

.HTop {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;

  /* border: solid rgb(255, 47, 206) 1px; */
}

.HTop img {
  width: 100%;
}

.HBottom {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
}

.HBottom h1 {
  color: var(--color3);
  font-size: 60px;
  padding: 0px;
  margin: 0px;
}

.HBottom h2 {
  font-size: 28px;
}

.HBottom p {
  font-size: 16px;
}

.HButtons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  /* border: solid greenyellow 1px; */
}

.HButtons img {
  height: 50px;
  margin-left: 25px;
  margin-right: 25px;
}

.HBuyButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  background: var(--color2);
  border: solid var(--color1) 3px;
  border-radius: 10px;
  padding: 10px 10px;
  margin-right: 25px;
  margin-left: 0px;
  color: var(--color3);
  font-size: 26px;
  font-weight: bold;
  cursor: pointer;
  transition: ease-in-out 0.3s;
}

.HBuyButton:hover {
  background: var(--color1);
  border: solid var(--color3) 3px;
  color: var(--color2);
}

/* .HSocials {
  border: solid greenyellow 1px;
} */

/*==========Media Queries==========*/

@media screen and (max-width: 1023px) {
  .HContainer {
    flex-direction: column;
    width: 90%;
  }

  .HButtons {
    flex-direction: column;
  }

  .HBuyButton {
    margin-left: 25px;
    margin-bottom: 25px;
    margin-top: 25px;
  }
}
