.TSuper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 0rem;

  /* border: solid greenyellow 1px; */
}

@keyframes bgcolor {
  0% {
    background: black;
  }
  33% {
    background: rgb(58, 44, 9);
  }
  50% {
    background: rgb(22, 0, 17);
  }
  66% {
    background: rgb(55, 8, 8);
  }
  100% {
    background: black;
  }
}

.TContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 70%;
  padding: 0px 0%;
}

.TCUp {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.TCUp h1 {
  color: var(--color2);
  font-size: 60px;
  padding: 0px;
  margin: 0px;
  text-shadow: 0 0 3px var(--color1), 0 0 7px var(--color1);
}

.TCUp h2 {
  font-size: 30px;
}

.TCDown {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.TCard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 290px;
  width: 290px;
  overflow: hidden;
  background-color: var(--color2);
  border: solid var(--color1) 6px;
  color: var(--color3);
  border-radius: 2rem;
  margin: 15px;
}

.TCard img {
  position: absolute;
  z-index: -2;
  height: 300px;
  transform: translateX(100px) translateY(50px);
}

.TCard h1 {
  font-size: 60px;
  padding: 0px;
  margin: 0px;
  text-shadow: 0 0 3px var(--color1), 0 0 7px var(--color1);
}

.TCard h2 {
  font-size: 26px;
  text-shadow: 0 0 3px var(--color1), 0 0 7px var(--color1);
}
