.NV2Super {
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 170px;
  right: 50px;
  bottom: 100px;
  z-index: 100;

  /* border: solid 2px violet; */
}

.NV2Container {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}

.NV2Container img {
  height: 50px;
}

@media screen and (max-width: 768px) {
  .NV2Super {
    right: 0px;
    bottom: 50px;
  }

  .NV2Container img {
    height: 40px;
  }
}
