.ASuper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem 0rem;
  margin: 0px;

  /* border: solid greenyellow 1px; */
}

.AContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 80%;

  /* border: solid greenyellow 1px; */
}

.ALeft {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ALeft img {
  width: 100%;
}

.ARight {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
}

.ARight span {
  color: var(--color3);
}

.ARight h1 {
  color: var(--color2);
  font-size: 60px;
  padding: 0px;
  margin: 0px;
  text-shadow: 0 0 3px var(--color1), 0 0 7px var(--color1);
}

.ARight h2 {
  font-size: 28px;
}

.ARight p {
  font-size: 22px;
}

.AButtons {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  /* border: solid greenyellow 1px; */
}

.AButtons img {
  height: 50px;
  margin-left: 25px;
  margin-right: 25px;
}

.ABuyButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  background: var(--color3);
  border: solid var(--color1) 2px;
  border-radius: 10px;
  padding: 10px 10px;
  margin-right: 25px;
  margin-left: 0px;
  color: var(--color1);
  font-size: 26px;
  font-weight: bold;
  cursor: pointer;
  transition: ease-in-out 0.3s;
}

.ABuyButton:hover {
  background: var(--color1);
  border: solid var(--color3) 2px;
  color: var(--color3);
}

/* .HSocials {
  border: solid greenyellow 1px;
} */

/*==========Media Queries==========*/

@media screen and (max-width: 1023px) {
  .AContainer {
    flex-direction: column;
    width: 90%;
  }

  .AButtons {
    flex-direction: column;
  }

  .ABuyButton {
    margin-left: 25px;
    margin-bottom: 25px;
    margin-top: 25px;
  }
}
